/* reset */
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video,
textarea,
button,
select,
input,
pre {
  margin: 0;
  padding: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
hr {
  display: none;
}
select {
  outline: none;
}
p,
ul,
ol,
li,
dl,
dt,
dd,
form {
  list-style-image: none;
  list-style-position: outside;
  list-style-type: none;
  border: 0px;
}
img,
fieldset,
iframe {
  border: 0 none;
}
button {
  border: 0 none;
  background-color: transparent;
  cursor: pointer;
}
address,
caption,
cite,
code,
dfn,
em,
var {
  font-style: normal;
  font-weight: normal;
}
a {
  text-decoration: none;
}

header.header-col {
  width: 100%;
  background-color: #fff;
  box-sizing: border-box;
}
.header-col .inner-col {
  display: flex;
  align-items: center;
  width: 1020px;
  margin: 0 auto;
  box-sizing: border-box;
}
.logo {
  display: inline-block;
  box-sizing: border-box;
}
.logo a {
  color: #000;
}
nav.nav-box {
  display: inline-block;
  width: 50%;
  height: 50px;
  margin-left: 11em;
  font-size: 110%;
  box-sizing: border-box;
}
.nav-box ul {
  display: table;
  table-layout: fixed;
  width: 100%;
}
.nav-box li {
  display: table-cell;
  list-style: none;
  text-align: center;
}
.nav-box li a {
  display: block;
  height: 50px;
  padding: 0.73em 0;
  color: #000;
  box-sizing: border-box;
}
.nav-box li.active a {
  border-bottom: 4px solid #258bf7;
}
.login-member-col {
  display: inline-block;
  margin-left: 4em;
  box-sizing: border-box;
}
.login-member-col li {
  display: inline-block;
}
.login-member-col li:after {
  content: '/';
  display: inline-block;
  margin: 0 0.5em;
}
.login-member-col li:last-child:after {
  display: none;
}

.login-member-col li a {
  text-transform: capitalize;
  color: #000;
}

