.nav-box .sub-list {
  display: none;
}
.sub-list.active {
  display: block;
  position: absolute;
  top: 2.8em;
  left: 0;
  width: 100%;
  padding: 1em 0 2em;
  background-color: #fff;
  text-align: center;
}
.nav-box .sub-list li a {
  color: #000;
}
.nav-box .sub-nav-box {
  display: none;
  background-color: #fff;
}
.nav-box .sub-nav-box.active {
  display: block;
}

.main-visual-col {
  display: block;
  width: 100%;
  height: 300px;
  background: #000
    url(https://wanted-marketing-image.s3.ap-northeast-2.amazonaws.com/service_introduction/event_list_banner/Global_Web_Event_Banner_A_20210215.jpg)
    no-repeat 50% 0;
  text-indent: -9999px;
}
.main-visual-col .inner-col {
  display: block;
  width: 100%;
  height: 100%;
}

.user-info-col {
  display: none;
}
.user-info-col.active {
  display: block;
}
.user-info-col .inner-col {
  width: 900px;
  margin: 0 auto;
}
.user-info-col ul {
  width: 100%;
  margin-top: 1em;
}
.user-info-col ul li {
  margin-bottom: 1em;
  box-shadow: 0.2em 0.2em 0.5em rgba(0, 0, 0, 0.2);
}

/* 데스크탑 */
.category {
  width: 80%;
  max-width: 1060px;
  margin: 0 auto;
  padding: 60px 0 0;
}
.category .slick-list button {
  width: 100%;
  height: 50px;
  padding: 0 20px;
  border: 1px solid transparent;
  border-radius: 5px;
  background-color: #f2f4f7;
  font-size: 15px;
}
.category .slick-list button:hover {
  background: #fff;
  color: #000;
  border: 1px solid #000;
}
.category .slick-prev,
.category .slick-next {
  background: none;
}
.category .slick-prev:before,
.category .slick-next:before {
  color: #000;
}
.category .slick-slide > div {
  padding-right: 10px;
}
.slick-slide img {
  margin: 0 auto;
}
.card-wrap {
  position: relative;
  width: 90%;
  max-width: 1060px;
  margin: 0 auto;
  padding: 0 0 60px;
  box-sizing: border-box;
}

.card-wrap h2 {
  margin: 60px 0 20px;
  font-size: 22px;
  font-weight: 900;
  line-height: 1.46;
  word-wrap: break-word;
  color: #333;
}
.card-wrap ul {
  overflow: hidden;
}
.card-wrap ul li {
  float: left;
  width: calc(25% - 10px);
  margin: 0 0 20px 10px;
  border: 1px solid #ececec;
  border-radius: 5px;
  overflow: hidden;
}
.card-wrap ul li:nth-child(4n + 1) {
  margin-left: 0;
}
.card-wrap ul li a {
  display: block;
}
.card-wrap ul li a .info {
  position: relative;
  padding: 20px;
  height: 180px;
}
.card-wrap ul li a .info .flag span {
  display: inline-block;
  margin: 0 5px 0 0;
  padding: 2px 5px;
  font-size: 12px;
  background: #000;
  color: #fff;
  border-radius: 5px;
}
.card-wrap ul li a .info h3 {
  margin: 10px 0 0;
  font-size: 18px;
  color: #333;
}
.card-wrap ul li a .info .date {
  position: absolute;
  left: 20px;
  bottom: 45px;
  width: calc(100% - 40px);
  margin-bottom: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-wrap: break-word;
  font-size: 12px;
  color: #767676;
}
.card-wrap ul li a .info .category {
  position: absolute;
  left: 20px;
  bottom: 20px;
  margin-top: 5px;
  height: 20px;
  width: calc(100% - 40px);
}
.card-wrap ul li a .info .category span {
  font-size: 11px;
}
.card-wrap ul li a .picture {
  position: relative;
  width: 100%;
  height: 0;
  padding: 0 0 70%;
  background-position: center;
  background-size: cover;
  background-color: #ddd;
}

.visual-col .slick-arrow {
  display: none !important;
}
.visual-col .slick-dots {
  bottom: 30px;
}
.visual-col a {
  display: block;
}
footer {
  padding: 30px 0 70px;
  background: #2b2b2b;
}
footer .inner-col {
  width: 80%;
  max-width: 1060px;
  margin: 0 auto;
}
footer .inner-col:after {
  display: block;
  content: '';
  clear: both;
}
footer .list-corp {
  margin: 30px 0;
}
footer .list-corp:after {
  display: block;
  content: '';
  clear: both;
}
footer .list-corp li {
  display: inline-block;
  margin: 0 0 0 30px;
}
footer .list-corp li:first-child {
  margin: 0;
}
footer .list-corp li a {
  color: #fff;
}
footer .lang-area {
  width: 200px;
  height: 36px;
  padding: 0 0 0 15px;
  background: #000;
  border-radius: 0;
  border: none;
  font-size: 14px;
  line-height: 36px;
  color: #fff;
}
